    left: 10px;
    top: 60px; }
  .content-wrapper .selector {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .content-wrapper .selector .handler {
      position: absolute;
      height: 12px;
      width: 12px;
      background-color: white;
      outline: solid 2px #273c92;
